import React from "react";
import { Spinner } from "reactstrap";

export const LoaderLG = (props) => {
  return (
    <div
      className={`position-fixed top-0 start-0 bottom-0 end-0 d-flex align-items-center justify-content-center ${props.pe}`}
    >
      <Spinner color="primary">Loading...</Spinner>
    </div>
  );
};
export const LoaderSM = (props) => {
  return (
    <div
      className={`position-fixed top-0 start-0 bottom-0 end-0 d-flex align-items-center justify-content-center ${props.pe}`}
    >
      <Spinner size="sm" color="primary">
        Loading...
      </Spinner>
    </div>
  );
};
