import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const initialState = {
  userdetails: localStorage.getItem("userdetails")
    ? JSON.parse(localStorage.getItem("userdetails"))
    : {},
  registeredUser: "",
  loading: false,
  error: "",
  success: "",
};

export const signupUserReducer = createAsyncThunk(
  "usersignup",
  async (body) => {
    const config = { headers: { "Content-type": "application/json" } };
    const { data } = await axios.post(`${API_URL}/api/user/`, body, config);
    return await data;
  }
);
export const sendOtpReducer = createAsyncThunk("send_otp", async (body) => {
  const config = { headers: { "Content-type": "application/json" } };
  const { data } = await axios.post(
    `${API_URL}/api/user/send_otp`,
    body,
    config
  );
  return await data;
});

export const verifyOtpReducer = createAsyncThunk("verify_otp", async (body) => {
  const config = { headers: { "Content-type": "application/json" } };
  const { data } = await axios.post(
    `${API_URL}/api/user/verify_otp`,
    body,
    config
  );
  return await data;
});

export const updatePasswordReducer = createAsyncThunk(
  "update_password",
  async (body) => {
    const config = { headers: { "Content-type": "application/json" } };
    const { data } = await axios.post(
      `${API_URL}/api/user/update_password`,
      body,
      config
    );
    return await data;
  }
);

export const loginUserReducer = createAsyncThunk("userlogin", async (body) => {
  const config = { headers: { "Content-type": "application/json" } };
  const { data } = await axios.post(`${API_URL}/api/user/login`, body, config);
  return await data;
});

export const logoutUserReducer = createAsyncThunk("userlogout", () => {
  return {};
});
export const clearError = createAsyncThunk("clearError", () => {
  return "";
});
export const clearSuccess = createAsyncThunk("clearSuccess", () => {
  return "";
});

export const updateProfileReducer = createAsyncThunk(
  "updateProfile",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
        token: initialState.userdetails.token,
      },
    };
    const { data } = await axios.put(
      `${API_URL}/api/user/update_profile`,
      body,
      config
    );
    return await data;
  }
);

export const getUserData = createAsyncThunk("getUserData", async (body) => {
  const config = { headers: { "Content-type": "application/json" } };
  const { data } = await axios.get(
    `${API_URL}/api/user/getUser/${body.id}`,
    config
  );
  return await data;
});

export const getUesrId = createAsyncThunk("getUserId", async (body) => {
  const config = { headers: { "Content-type": "application/json" } };
  const { data } = await axios.post(`${API_URL}/api/user/getId`, body, config);
  return data;
});

const authReducer = createSlice({
  name: "user",
  initialState,
  reducers: {},
  extraReducers: {
    [signupUserReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        state.registeredUser = action.payload.email;
        // localStorage.setItem(
        //   "registeredUser",
        //   JSON.stringify(action.payload.email),
        //   5000
        // );
        state.success =
          "User Registration successful!! OTP has been sent to your email!!";
      }
    },
    [signupUserReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [signupUserReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [sendOtpReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        state.registeredUser = action.payload.email;
        state.success = "Otp has been sent to your email!!";
      }
    },
    [sendOtpReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [sendOtpReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [verifyOtpReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        if (action.payload.forgotUser) {
          state.registeredUser = action.payload.forgotUser;
          state.success = "OTP verified successfully!! Change your password.";
        } else {
          state.userdetails = action.payload;
          localStorage.setItem("userdetails", JSON.stringify(action.payload));
          state.registeredUser = "";
          state.success = "";
        }
      }
    },
    [verifyOtpReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [verifyOtpReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [updatePasswordReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        state.userdetails = action.payload;
        localStorage.setItem("userdetails", JSON.stringify(action.payload));
        state.registeredUser = "";
        state.success = "Password Updated Successful!!";
      }
    },
    [updatePasswordReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [updatePasswordReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [loginUserReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        state.userdetails = action.payload;
        localStorage.setItem("userdetails", JSON.stringify(action.payload));
        //state.success = "User Registration successful";
      }
    },
    [loginUserReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [loginUserReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [updateProfileReducer.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.success = "";
      } else {
        state.userdetails = action.payload;
        localStorage.setItem("userdetails", JSON.stringify(action.payload));
        state.success = "User Profile Updated successful";
      }
    },
    [updateProfileReducer.pending]: (state, action) => {
      state.loading = true;
      state.error = "";
      state.success = "";
    },
    [updateProfileReducer.rejected]: (state, action) => {
      state.loading = false;
      state.success = "";
      if (action.payload.error) {
        state.error = action.payload.error;
      }
    },
    [logoutUserReducer.fulfilled]: (state, action) => {
      state.loading = false;
      state.success = "";
      state.userdetails = action.payload;
      state.homeScreen = action.payload;
      localStorage.removeItem("userdetails");
    },
    [clearError.fulfilled]: (state) => {
      state.error = "";
    },
    [clearSuccess.fulfilled]: (state) => {
      state.success = "";
    },
    [getUserData.fulfilled]: (state) => {
      state.success = "";
    },
    [getUesrId.fulfilled]: (state) => {
      state.success = "";
    },
  },
});

export default authReducer.reducer;
