import React, { Suspense } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
//import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { Route, Routes } from "react-router-dom";
import Store from "./store";
import { Provider } from "react-redux";

import ScrollToTop from "./utils/ScrollToTop";
import { LoaderLG } from "./shared-components/loader";
const Home = React.lazy(() => import("./views/Home"));
const Portfolios = React.lazy(() => import("./views/Portfolios"));
const Blog = React.lazy(() => import("./views/Blog"));
const Components = React.lazy(() => import("./views/Components"));
const Skills = React.lazy(() => import("./views/Skills"));
const SkillsQnA = React.lazy(() => import("./views/SkillsQnA"));
const Services = React.lazy(() => import("./views/Services"));
const ServicesQnA = React.lazy(() => import("./views/ServicesQnA"));
const Contacts = React.lazy(() => import("./views/Contacts"));
const Login = React.lazy(() => import("./views/Login"));
const Signup = React.lazy(() => import("./views/Signup"));
const Security = React.lazy(() => import("./views/Security"));
const ChangePassword = React.lazy(() => import("./views/ChangePassword"));
const Profile = React.lazy(() => import("./views/Profile"));
const ProtectedPage = React.lazy(() =>
  import("./shared-components/ProtectedPage")
);
const ProfileServices = React.lazy(() =>
  import("./shared-components/ProfileServices")
);

function App() {
  return (
    <Provider store={Store}>
      <div className="app">
        <ScrollToTop />
        <Routes>
          <Route
            path="/"
            exact
            element={
              <Suspense fallback={<LoaderLG />}>
                <Home />
              </Suspense>
            }
          />
          <Route
            path="/home"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Home />
              </Suspense>
            }
          />
          <Route
            path="/skills"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Skills />
              </Suspense>
            }
          />
          <Route
            path="/skills/:id"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Skills />
              </Suspense>
            }
          />
          <Route
            path="/skillsQnA/:skillId"
            element={
              <ProtectedPage>
                <Suspense fallback={<LoaderLG />}>
                  <SkillsQnA />
                </Suspense>
              </ProtectedPage>
            }
          />
          <Route
            path="/services"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Services />
              </Suspense>
            }
          />
          <Route
            path="/services/:id"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Services />
              </Suspense>
            }
          />
          <Route
            path="/servicesQnA/:serviceId"
            element={
              <ProtectedPage>
                <Suspense fallback={<LoaderLG />}>
                  <ServicesQnA />
                </Suspense>
              </ProtectedPage>
            }
          />
          <Route
            path="/portfolios"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Portfolios />
              </Suspense>
            }
          />
          <Route
            path="/blog"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Blog />
              </Suspense>
            }
          />
          <Route
            path="/blog/:id"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Blog />
              </Suspense>
            }
          />
          <Route
            path="/components"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Components />
              </Suspense>
            }
          />
          <Route
            path="/components/:level1"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Components />
              </Suspense>
            }
          />
          <Route
            path="/components/:level1/:level2"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Components />
              </Suspense>
            }
          />
          <Route
            path="/components/:level1/:level2/:level3"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Components />
              </Suspense>
            }
          />
          <Route
            path="/contact"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Contacts />
              </Suspense>
            }
          />
          <Route
            path="/login"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Login />
              </Suspense>
            }
          />
          <Route
            path="/profile"
            element={
              <Suspense fallback={<LoaderLG />}>
                <ProtectedPage>
                  <Profile />
                </ProtectedPage>
              </Suspense>
            }
          />
          <Route
            path="/profile/services"
            element={
              <Suspense fallback={<LoaderLG />}>
                <ProtectedPage>
                  <ProfileServices />
                </ProtectedPage>
              </Suspense>
            }
          />
          <Route
            path="/signup"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Signup />
              </Suspense>
            }
          />
          <Route
            path="/security"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Security />
              </Suspense>
            }
          />
          <Route
            path="/change_password"
            element={
              <Suspense fallback={<LoaderLG />}>
                <ChangePassword />
              </Suspense>
            }
          />
          <Route
            path="*"
            element={
              <Suspense fallback={<LoaderLG />}>
                <Home />
              </Suspense>
            }
          />
        </Routes>
      </div>
    </Provider>
  );
}

export default App;
