import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
// import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

export const getBase64ImageReducer = createAsyncThunk(
  "getBase64ImageReducer",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/common/base64Image/`,
      body,
      config
    );
    return await data;
  }
);

export const setLoading = createAsyncThunk("setloading", async (loading) => {
  return loading;
});
export const setSuccess = createAsyncThunk("setsuccess", async (success) => {
  return success;
});
export const clearSuccess = createAsyncThunk(
  "clearSuccess",
  async (success) => {
    return success;
  }
);
export const setError = createAsyncThunk("setError", async (error) => {
  return error;
});
export const clearError = createAsyncThunk("clearError", async (error) => {
  return error;
});

const initialState = {
  loading: false,
  error: "",
  success: "",
};

const commonReducer = createSlice({
  name: "commonReducer",
  initialState,
  reducers: {},
  extraReducers: {
    [getBase64ImageReducer.fulfilled]: (state, action) => {},
    [setLoading.fulfilled]: (state, action) => {
      if (action.payload) {
        state.loading = true;
      } else {
        state.loading = false;
      }
    },
    [setSuccess.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload) {
        const index = state.success.includes(action.payload);

        if (!index) {
          state.success = [...state.success, action.payload];
        }
      }
    },
    [clearSuccess.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload) {
        const index = state.success.indexOf(action.payload);
        if (index !== -1) {
          state.success.splice(index, 1);
        }
      }
    },
    [setError.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload) {
        const index = state.error.includes(action.payload);
        if (!index) {
          state.error = [...state.error, action.payload];
        }
      }
    },
    [clearError.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload) {
        const index = state.error.indexOf(action.payload);
        if (index !== -1) {
          state.error.splice(index, 1);
        }
      }
    },
  },
});

export default commonReducer.reducer;
