import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

export const getHomeData = createAsyncThunk(
  "getHomeData",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = req.token
        ? await axios.get(
            `${API_URL}/api/home/details/${req.token}`,
            body,
            config
          )
        : await axios.get(`${API_URL}/api/home/details/`, body, config);
      return await data;
    } catch (error) {
      return error;
    }
  }
);

export const updateHomeReducer = createAsyncThunk(
  "updateHome",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.put(
      `${API_URL}/api/home/update/`,
      body,
      config
    );
    return await data;
  }
);

const initialState = {};

const homeReducer = createSlice({
  name: "homeScreen",
  initialState,
  reducers: {},
  extraReducers: {
    [getHomeData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.homeScreen = action.payload;
        state.success = "";
      }
    },
    [getHomeData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreen = null;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [getHomeData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreen = null;
        state.homeScreenError = action.payload.error;
      }
    },
    [updateHomeReducer.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.homeScreen = action.payload;
        state.homeScreenSuccess = "Home page data updated successful!!";
      }
    },
    [updateHomeReducer.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreen = null;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [updateHomeReducer.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreen = null;
        state.homeScreenError = action.payload.error;
      }
    },
  },
});

export default homeReducer.reducer;
